<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">订单号添加</div>
      <div class="modal-body">
        <h3>基本信息</h3>

        <div class="row">
          <div class="columns">
            <span class="label">采购单ID:</span>
            <span class="value">{{ form.id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">采购单号:</span>
            <a-input v-model="form.purchase_id" style="width: 200px;" />
          </div>
        </div>
      </div>
    </a-modal>
  </div>
</template>

<script>
import {fillThePurchaseIdForPurchaseOrder } from '@/api/purchaser'
import { message } from "ant-design-vue";

export default {
  name: 'ApprovalFormMoal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      order_packages : [],
    };
  },
  computed: {

  },
  methods: {
    confirm() {
      fillThePurchaseIdForPurchaseOrder({"purchase_order_id": this.form.id, "purchase_id": this.form.purchase_id}).then(
        res => {
          message.success(res.msg)
          this.$emit('update', false);
        }
      )

    },
    cancel() {
      this.$emit('cancel', false);
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns .label{
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}

button {
  margin-right: 2px;
}
</style>